.center-custom {
  position: relative;
  margin: 0 auto;
  width: 34vw;
}

.center-custom-percentage {
  position: relative;
  margin: 0 auto;
  width: 100%;
}

.hello-h{
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 847px;
  stroke-dashoffset: 847px;
}

.hello-e{
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 263px;
  stroke-dashoffset: 263px;
}

.hello-l{
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 387px;
  stroke-dashoffset: 387px;
}

.hello-o{
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 205px;
  stroke-dashoffset: 205px;
}

.change-color{
  fill: #f1c40f;
  stroke: #f1c40f;
}

.burst-before{
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 113px;
  stroke-dashoffset: 113px;
  fill: none;
  stroke-width: 13px;
  transition: all 0.3s ease-in;
  opacity: 1;
}

.burst{
  stroke-dasharray: 112px;
  stroke-dashoffset: -112px;
}

.wrapper{
  width: 100vw;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: auto;
}


#burst{
  width: 55vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  overflow: visible;
}
